body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: white !important;
}

.login-screen {
    background-color: white !important;
}   

.login-left img {
    width: 90%;
}

.login-box {
    background-color: white;
    align-items: center;
    flex: 1 1;
}

.login-form-button {
    width: 100%;
}


.register-screen {
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content: center;
}   

.register-screen .landing-intro-screen .signin-left {
    max-height: 488px;
}

.register-box {
    background-color: white;
    max-width: 900px;
    flex: 1 1;
    border-radius: 10px;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.08);
    overflow: hidden;
}



.dashboard-page form label{
	margin-bottom: 0 !important;
	line-height: normal !important;
	line-height: initial !important;
}

.filig_html {
	height: 500px;
	overflow: auto;
	padding: 10px;
	/*overflow-x: hidden;*/
}

.loader {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 500px;
}

.round-border {
	border-radius: 20px;
}

mark {
	background-color: #e7ed54 !important;
}

.banner {
	border-radius: 10px;
	margin-bottom: -35px;
}

.header-search {
	height: 70px;
	max-width: 800px;
	border-radius: 10px;
	overflow: hidden;
	box-shadow: 5px 10px 18px rgba(0,0,0,0.2) !important;
	border: none !important;
}

.header-search input {
	border: 10px !important;
}

.filing-list h4 {
	color: #4A5B8E !important;
}

.filter-form .ant-calendar-picker-input.ant-input,
.filter-form input {
	border-color: #B4B9CC !important;
}

.filter-form label {
    white-space: pre-line;
    text-align: left;
}

.ant-input:not(:first-child) {
	padding-left: 80px !important;
}

.ant-list-vertical .ant-list-item {
	border: 1px solid #D9D9D9 !important;
	padding: 20px;
	margin-bottom: 20px;
	border-radius: 6px;
	color: #4A5B8E;
}

.viewmore-btn {
	background: #8bd8bd73 !important;
	color: #42a482 !important;
	border: none !important;
	box-shadow: 0 2px 5px rgba(0,0,0,0.2) !important;
}
.App {
  /* text-align: center; */
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

